@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: lavender;
  background: linear-gradient(110deg, #69b7eb, #b3dbd3, #c6baff);
  font-size: 17px;
  font-family: Audiowide, sans-serif;
  height: 100%;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',s
    monospace;
}

.App {
  display:flex;
  align-items: center;
  min-height: 90vh;
  margin-bottom:10vh;
}



.Todo {
    display: flex;
    margin: 0 -3rem 4px;
    padding: 1.1rem 3rem;
    height: 60px;
    justify-content: space-between;
    align-items: center;
    background: rgba(255, 255, 255, 0.2);
    overflow: hidden;
    position: relative;
    font-size: 1.3rem;
    transition: opacity 500ms ease-in-out;
  }

  .Todo-task {
    position: relative;
    transition: opacity 0.4s linear;
    display: flex;
    justify-content: space-between;
  }

  .Todo-task::selection {
    background: none;
  }
  
  .Todo-task::-moz-selection {
    background: none;
  }

  .Todo-task.completed {
    transition: opacity 300ms ease-in-out;
    transition-delay: 200ms;
    opacity: 0.7;
  }
  

  .Todo-task.completed:before {
    content: "";
    position: absolute;
    top: 50%;
    left: -0.7rem;
    width: 0%;
    height: 3px;
    background: white;
    animation: strike 0.4s forwards cubic-bezier(0.34, 1.56, 0.64, 1);
  }
  
  @keyframes strike {
	100% { 
        width:calc(100% + 1.5rem); 
    }
}
  
  .Todo-buttons {
    flex-shrink: 0;
    padding-left: 0.7em;
    margin-left: auto;
  }
  
  .Todo-buttons button {
    border: none;
    font-size: 1em;
    margin: 0.4em;
    background: none;
    -webkit-appearance: none;
    cursor: pointer;
    color: #fff;
  }
  
  .Todo-edit-form {
    display: flex;
    flex-wrap: wrap;
  }
  .Todo-edit-form label {
    min-width: 100%;
    margin-bottom: 0.5rem;
    font-size: 1.3rem;
  }
  .Todo-edit-form input {
    flex-grow: 1;
    border: none;
    color:rgba(0, 0, 0,0.75);
    background: #fbfcff;
    padding: 0 1.5em;
    font-size: medium;
    font-size: initial;
    box-shadow: inset 0 0 2px rgb(0, 0, 0);
  }

  .Todo-edit-form button {
    padding: 0 1.3rem;
    border: none;
    background: rgba(70, 130, 180, 0.2);
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    border: 1px solid rgba(255, 255, 255, 0.3);
    margin-left: 5px;
    cursor: pointer;
    transition: background 0.2s ease-out;
  }
  .Todo-edit-form button:hover {
    background: steelblue;
  }
  .Todo-edit-form input,
  .Todo-edit-form button {
     letter-spacing: 2px;
     font-family: "Quicksand", sans-serif;
     height: 3rem;
  }
  
.NewTodoForm {
    margin-top: 1.5rem;
    display: flex;
    flex-wrap: wrap;
  }
  .NewTodoForm label {
    min-width: 100%;
    margin-bottom: 0.5rem;
    font-size: 1.3rem;
  }
  .NewTodoForm input {
    flex-grow: 1;
    border: none;
    background: #f7f1f1;
    padding: 0 1.5em;
    font-size: medium;
    font-size: initial;
    outline: none;
  }
  .NewTodoForm button {
    padding: 0 1.3rem;
    border: none;
    background: rgba(255, 255, 255, 0.2);
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    border: 1px solid rgba(255, 255, 255, 0.3);
    margin-left: 5px;
    cursor: pointer;
    transition: background 0.4s ease-out;
    outline: none;
  }
  .NewTodoForm button:hover {
    background: rgba(0, 0, 0, 0.1);
  }
  .NewTodoForm input,
  .NewTodoForm button {
    letter-spacing: 1.7px;
    font-family: "Quicksand", sans-serif;
    height: 3rem;
  }
  
  .TodoList {
    margin: 4rem auto;
    padding: 2rem 3rem 3rem;
    min-width: 400px;
    max-width: 520px;
    background: rgb(70, 130, 180,0.95);
    color: #fff;
    box-shadow: -20px -20px 0px 0px rgba(100, 100, 100, 0.1);
    overflow: hidden;
    border-radius: 5px;
  }
  .TodoList ul {
    margin-top: 2.6rem;
    list-style: none;
  }
  
  .TodoList h1 {
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05em;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  }
  .TodoList h1 span {
    display: block;
    font-size: 0.83rem;
    margin-bottom: 0.15rem;
    margin-left: 3px;
    margin-top: 0.3rem;
  }
  .TodoList p {
    font-weight: normal;
    letter-spacing: 0.05em;
    display: block;
    font-size: 15.4px;
    opacity:0.4;
    margin-bottom: 0.7rem;
    margin-left: 3px;
    transform: scaleY(0.9) rotateX(180deg);
  }

  .Todo .Todo-buttons i {
    display: inline-block;
  }

